<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-11">
          <div class="rounded-lg p-3">
            <!-- <button type="button" @click="$router.push(`/admin/kategori/addMarker?id=${vdata.id}&nama=${vdata.kategori}`)" v-if="vdata.id" class="btn btn-sm btn-dark  float-right"><span class="typcn typcn-pin"></span> Add Marker</button>
            <button type="button" @click="$router.push(`/admin/kategori/floorplan?id=${vdata.id}&nama=${vdata.kategori}`)" v-if="vdata.id" class="btn btn-sm btn-dark  float-right ml-2 mr-2"><span class="typcn typcn-pin"></span> Indoor Floor Plan</button>
            <button type="button" @click="$router.push(`/admin/kategori/geojson?id=${vdata.id}&nama=${vdata.kategori}`)" v-if="vdata.id" class="btn btn-sm btn-dark  "><span class="typcn typcn-map"></span> Add GeoJson</button>
            <button type="button" @click="$router.push(`/admin/kategori/room?id=${vdata.id}&nama=${vdata.kategori}`)" v-if="vdata.id" class="btn btn-sm btn-dark ml-2 "><span class="typcn typcn-map"></span> Room</button> -->
            <button type="button"  class="btn btn-sm btn-style9 float-right" v-if="vdata.id" @click="pilihpeta(vdata.id,vdata.kategori)">Ganti Peta</button>
            <br>
            <hr class="mt-2 mb-2">
            <p class="float-right">Peta yang dipilih : 
                <button type="button"  @click="$router.push(`/office?id=2sz3jsdn9ndanas2&token=Aew${uniqid}&cat=${$store.state.data.idpeta}&idpeta=${uniqid}-x23asodjiwdw`)" class="btn btn-sm btn-success  ">
                    {{$store.state.data.kategori}}
                </button>
            </p>
            <div v-if="$store.state.data.kategori">
              <button type="button" @click="$router.push(`/admin/floorplan?id=${$store.state.data.idpeta}`)" class="btn btn-sm btn-dark  ml-1">+ Floorplan</button>
              <button type="button" @click="$router.push(`/admin/kategori/floorplan?id=${$store.state.data.idpeta}&nama=${$store.state.data.kategori}`)" class="btn btn-sm btn-dark  ml-1">+ Marker</button>
              <button type="button" @click="$router.push(`/admin/kategori/room?id=${$store.state.data.idpeta}&nama=${$store.state.data.kategori}`)" class="btn btn-sm btn-dark  ml-1">+ Data Rumah/Ruang</button>
              <button type="button" @click="$router.push(`/admin/kategori/360data?id=${$store.state.data.idpeta}&nama=${$store.state.data.kategori}`)" class="btn btn-sm btn-dark  ml-1">+ 360 Data</button>
              <hr class="mt-1 mb-1">
              <button type="button" @click="$router.push(`/admin/kategori/floorplan?id=${$store.state.data.idpeta}&nama=${$store.state.data.kategori}&geo=true`)" class="btn btn-sm btn-dark  ml-1">Create GeoJSON</button>
              <button type="button" @click="$router.push(`/admin/kategori/geojson?id=${$store.state.data.idpeta}&nama=${$store.state.data.kategori}`)" class="btn btn-sm btn-dark  ml-1">Upload Geojson</button>
              <button type="button" @click="$router.push(`/admin/fasilitas?id=${$store.state.data.idpeta}&nama=${$store.state.data.kategori}`)" class="btn btn-sm btn-style2  ml-1">List Fasilitas</button>
            </div>
            <button type="button"  class="btn btn-sm btn-dark float-right" @click="tambahpeta=!tambahpeta">Map Add +</button>
            <p class="font-semibold text-lg p-3">List Peta/Map 
              <img src="https://upload.egov.balangankab.go.id/svg/map.svg" 
              class="d-inline" style="height:20px;width:20px;" alt=""></p>
              <hr>
          <div class="row justify-content-center" v-if="tambahpeta">
            <div class="col-md-10">
              <div class="row">
                <div class="col-6">
                  <div class="sm-form ">
                      <label for="kategori">Kategori Peta/Map : </label>
                      <input required type="text" id="kategori" name="kategori" class="form-control form-control-sm" placeholder="kategori" v-model="vdata.kategori" >
                  </div>
                  <div class="sm-form ">
                      <label for="sumber">Sumber Data Peta : </label>
                      <input type="text" id="sumber" name="sumber" class="form-control form-control-sm" placeholder="sumber" v-model="vdata.sumber" >
                  </div>
                  <div class="sm-form ">
                      <label for="deskripsi">Deskripsi : </label>
                      <div class="sm-form">
                      <textarea type="text" id="deskripsi" name="deskripsi" rows="2" placeholder="deskripsi..." class="form-control md-textarea" v-model="vdata.deskripsi"
                      ></textarea>
                      </div>
                  </div>

                </div>
                <div class="col-6">
                  <div class="sm-form ">
                      <label for="tag">Tag : </label>
                      <input type="text" id="tag" name="tag" class="form-control form-control-sm" placeholder="tag" v-model="vdata.tag" >
                  </div>
                  <div class="sm-form ">
                      <label for="group">Type : </label>
                      <select class='form-control' v-model="vdata.type">
                        <option>Real Estate</option>
                        <option>Wisata</option>
                      </select>
                  </div>
                  <div class="sm-form ">
                      <label for="gambar">Your gambar</label>
                      <input type="file" id="filenya" name="gambar" class="form-control form-control-sm" placeholder="gambar" @change="upload" >
                      <input type="text" id="gambar" name="gambar" class="form-control form-control-sm" placeholder="gambar" v-model="vdata.gambar" >
                  </div>
                </div>
              </div>
              <!-- BUTTON PART -->
          <div class="mt-3 p-3">
              <button
                type="submit"
                class="btn btn-sm"
                :class="
                  btn == 'tambah'
                    ? 'btn-primary'
                    : btn == 'update'
                    ? 'btn-success'
                    : 'btn-danger'
                "
              >
                <span class="typcn typcn-info"></span>  Proses {{ btn }}
              </button>
              <div class="float-right">
                <button
                  type="button"
                  v-show="btn!='tambah'"
                  @click="
                    btn = 'tambah';
                    vdata = {};
                  "
                  class="btn btn-sm btn-dark"
                >
                  <span class="typcn typcn-edit"></span> Tambah
                </button>
                <button
                  type="button"
                  @click="btn = 'delete'"
                  v-show="btn!='tambah'"
                  class="ml-3 btn btn-sm btn-danger"
                >
                  <span class="typcn typcn-delete"></span> Delete
                </button>
              </div>
            </div>
            <!-- END BUTTON PART -->
            </div>
          </div>
            <div v-if="datanya.length>0" class="p-3">
              <Btables :tables="datanya"  :cari="['']" :hide="['latitude','longitude','gambar']" @selected="ambil" ref="btable" />
            </div>
   
        </div>
      </div>
      </div>
    </form>
    <!-- TABLE PART -->
    <div class="row justify-content-center">
      <div class="col-md-9 sm:ml-5">
            
      </div>
    </div>
    <!-- END TABLE PART -->
  </div>
</template>

<script>
// TOTURIAL 
import uniqid from 'uniqid'
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
export default {
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      tambahpeta:false,
      vdata: {},
      uniqid:'',
      btn: "tambah",
      datanya:[],
    };
  },
  methods: {
    upload(){
      let file = document.querySelector("#filenya"); // berikan id pada input file
      sdb.collection('tbuser',false).upload(file,true,true) // file,compress,scramble
      .then(res=>{
          this.vdata.gambar=res;
          this.$forceUpdate();
      })
    },
    pilihpeta(id,kategori){
      var reload=0;
      if(!this.$store.state.data.kategori || this.$store.state.data.kategori=='false'){
        reload=1;
      }
      localStorage.setItem('idpeta',id);
      localStorage.setItem('kategori',kategori);
      sdb.alert(`Peta telah di update menjadi ${kategori}!`,'bg-green-400')
      this.$store.state.data.idpeta=id;
      this.$store.state.data.kategori=kategori;
      if(reload==1){
        setTimeout(() => {
          this.$router.go()
        }, 1000);
      }
      this.$forceUpdate();
    },
    upload(){
        let that=this;
        let file = document.querySelector("#filenya"); // berikan id pada input file
        sdb.collection('tbuser').upload(file).then(res=>{
           that.vdata.geojson=res
           that.$forceUpdate();
        })
    },
    test(){
      this.$refs.btable.getData()
    },
    getData(){
      sdb.collection("app_gis_kategori").doc().select(`select * from app_gis_kategori`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
      
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    submit() {
      let that=this
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          //idnya letak di vdata
          delete this.vdata.id;
          sdb
            .collection("app_gis_kategori")
            .doc()
            .set(this.vdata)
            .then((res) => {
              that.getData();
            // that.$nuxt.$emit('getData')

            });
        } else if (this.btn == "update") {
          sdb
            .collection("app_gis_kategori")
            .doc()
            .set(this.vdata)
            .then((res) => {
              that.getData();
            // that.$nuxt.$emit('getData')
            });
        } else if (this.btn == "delete") {
          sdb
            .collection("app_gis_kategori")
            .doc(this.vdata.id)
            .delete()
            .then((res) => {
              that.getData();
            // that.$nuxt.$emit('getData')
            });
        }
        // this.$nuxt.$emit('getData','ok')
      }
    },
  },
  async mounted() {
    this.getData();
    this.uniqid=uniqid();

  },
layout:'app_admin',
};
</script>